.App {
  text-align: center;
  background-color: aquamarine;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: linear-gradient(to bottom right,white, rgb(128, 198, 206),rgb(128, 198, 206),rgb(0, 238, 255), white, rgb(0, 238, 255));


}

main{
  /* background-color: aquamarine; */
  /* border: 5px red solid; */
  min-height: 90vh;
  margin: calc(100px + 2vmin) auto;
  width:calc(30vw + 45vmin);
  background-image: linear-gradient(to bottom right,white, rgb(128, 198, 206),rgb(128, 198, 206),rgb(0, 238, 255), white, rgb(0, 238, 255));
  box-shadow: -10px 10px 50px 10px;
  border-radius: 10px;
  
}

